import { getFilesPaneWidths } from "./getFilesPaneWidths"

const testMatrix = [
  {
    isChatPaneOpen: false,
    isDocumentPaneExpanded: false,
    isDocumentPaneOpen: true,
    expected: { chatPaneWidth: null, documentPaneWidth: "50%", filesPaneWidth: "50%" },
  },
  {
    isChatPaneOpen: false,
    isDocumentPaneExpanded: true,
    isDocumentPaneOpen: true,
    expected: { chatPaneWidth: null, documentPaneWidth: "100%", filesPaneWidth: null },
  },
  {
    isChatPaneOpen: true,
    isDocumentPaneExpanded: false,
    isDocumentPaneOpen: false,
    expected: { chatPaneWidth: "50%", documentPaneWidth: null, filesPaneWidth: "50%" },
  },
  {
    isChatPaneOpen: true,
    isDocumentPaneExpanded: false,
    isDocumentPaneOpen: true,
    expected: { chatPaneWidth: "33%", documentPaneWidth: "33%", filesPaneWidth: "34%" },
  },
  {
    isChatPaneOpen: false,
    isDocumentPaneExpanded: false,
    isDocumentPaneOpen: false,
    expected: { chatPaneWidth: null, documentPaneWidth: null, filesPaneWidth: "100%" },
  },
]

describe("getFilesPaneWidths", () => {
  describe.each(testMatrix)(
    "when isChatPaneOpen is $isChatPaneOpen, isDocumentPaneExpanded is $isDocumentPaneExpanded and isDocumentPaneOpen is $isDocumentPaneOpen",
    ({ isChatPaneOpen, isDocumentPaneExpanded, isDocumentPaneOpen, expected }) => {
      it(`should return ${JSON.stringify(expected)}`, () => {
        expect(
          getFilesPaneWidths({ isChatPaneOpen, isDocumentPaneExpanded, isDocumentPaneOpen }),
        ).toStrictEqual(expected)
      })
    },
  )
})
